.customBackgroundContainer {
  position: relative;
  border-radius: 2rem;
  overflow: hidden; /* Arka plann tamasn engeller */
}

.customBackground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.7); /* Hafif beyaz tonlu effaf arka plan */
  backdrop-filter: blur(10px); /* Bulank arka plan */
  z-index: -1; /* Arka plann ierikten geride kalmas */
}
